'use client'

import Link from "next/link"

export default function FeaturesPage() {
    return (
        <main className="bg-white">

            {/* ---------- HERO ---------- */}
            <section className="max-w-7xl mx-auto px-6 py-20 text-center">
                <h1 className="text-4xl md:text-5xl font-bold text-primary mb-6">
                    Everything You Need to Build Customer Loyalty
                </h1>
                <p className="text-lg text-gray-600 max-w-3xl mx-auto">
                    Stampi helps local businesses reward customers, increase repeat visits,
                    and grow without complicated apps or hardware.
                </p>
            </section>

            {/* ---------- CORE FEATURES ---------- */}
            <section className="bg-light-shade/60 py-20">
                <div className="max-w-7xl mx-auto px-6 grid md:grid-cols-3 gap-10">

                    <FeatureCard
                        title="Digital Stamp Cards"
                        description="Replace paper stamp cards with a simple digital system customers love."
                    />

                    <FeatureCard
                        title="UPI Payment Verification"
                        description="Automatically verify payments via UPI screenshots or transaction data."
                    />

                    <FeatureCard
                        title="Rewards & Redemptions"
                        description="Create rewards that motivate customers to come back again and again."
                    />

                    <FeatureCard
                        title="Customer Insights"
                        description="See repeat visits, rewards redeemed, and customer engagement in one dashboard."
                    />

                    <FeatureCard
                        title="No App for Customers"
                        description="Customers don’t need to install anything — works directly with QR & web."
                    />

                    <FeatureCard
                        title="Easy Setup"
                        description="Get started in minutes. No hardware, no training, no complexity."
                    />

                </div>
            </section>

            {/* ---------- HOW IT WORKS ---------- */}
            <section className="max-w-7xl mx-auto px-6 py-20">
                <h2 className="text-3xl font-semibold text-center mb-14">
                    How Stampi Works
                </h2>

                <div className="grid md:grid-cols-3 gap-10 text-center">
                    <Step number="1" title="Customer Pays">
                        Customer pays via UPI or cash and scans your QR.
                    </Step>
                    <Step number="2" title="Stamp Added">
                        Stampi verifies payment and adds a digital stamp.
                    </Step>
                    <Step number="3" title="Reward Earned">
                        Customer redeems rewards and comes back again.
                    </Step>
                </div>
            </section>

            {/* ---------- BENEFITS ---------- */}
            <section className="bg-light-shade/60 py-20">
                <div className="max-w-4xl mx-auto px-6 text-center">
                    <h2 className="text-3xl font-semibold mb-6">
                        Built for Local Businesses
                    </h2>
                    <p className="text-gray-600 text-lg">
                        Whether you run a café, salon, grocery store, or retail shop,
                        Stampi helps you build loyalty without extra work.
                    </p>
                </div>
            </section>

            {/* ---------- CTA ---------- */}
            <section className="py-20">
                <div className="max-w-4xl mx-auto px-6">
                    <div className="bg-primary rounded-2xl text-white text-center py-14 px-6">
                        <h2 className="text-3xl font-semibold mb-4">
                            Start Building Customer Loyalty Today
                        </h2>
                        <p className="mb-8 text-white/90">
                            Join shopkeepers using Stampi to increase repeat customers.
                        </p>
                        <Link
                            href="/signup"
                            className="inline-block bg-white text-primary font-semibold px-8 py-3 rounded-lg hover:bg-gray-100 transition"
                        >
                            Start Free Trial
                        </Link>
                    </div>
                </div>
            </section>

        </main>
    )
}

/* ---------- COMPONENTS ---------- */

const FeatureCard = ({ title, description }) => {
    return (
        <div className="bg-white rounded-xl p-6 shadow-sm hover:shadow-md transition">
            <h3 className="text-lg font-semibold mb-3">{title}</h3>
            <p className="text-gray-600 text-sm leading-relaxed">
                {description}
            </p>
        </div>
    )
}

const Step = ({ number, title, children }) => {
    return (
        <div>
            <div className="w-12 h-12 mx-auto mb-4 rounded-full bg-primary text-white flex items-center justify-center font-bold">
                {number}
            </div>
            <h3 className="font-semibold mb-2">{title}</h3>
            <p className="text-gray-600 text-sm">{children}</p>
        </div>
    )
}
